:root {
  --font-family: "Montserrat", sans-serif;
  --content-width: 1446px;
  --content-width-narrow: 1176px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --container-width-narrow: calc(var(--content-width-narrow) + (var(--container-offset) * 2));
  --accent-color: #de596d;
  --red-hover: #bc1340;
  --black-color: #161516;
  --dark-grey-color: #222022;
  --pink: #dd8e94;
  --pink-color: #ffd7da;
  --pink-light: #fff4f8;
  --white-color: #fff;
}

/* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../Montserrat-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../Montserrat-SemiBold.woff") format("woff");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../Montserrat-Medium.woff") format("woff");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "NunitoSans";
  src: url("../fonts/NunitoSans-Light.woff2") format("woff2"), url("../NunitoSans-Light.woff") format("woff");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2"), url("../OpenSans-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "SourceSansPro";
  src: url("../fonts/SourceSansPro-Regular.woff2") format("woff2"), url("../SourceSansPro-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul,
ol {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

ul,
li {
  list-style: none;
}

img {
  height: auto;
  max-width: 100%;
  display: block;
  object-fit: cover;
}

input,
button,
textarea,
select {
  font: inherit;
  border: none;
}

button {
  color: inherit;
  cursor: pointer;
  background-color: transparent;
  border: none;
  padding: 0;
}

a {
  text-decoration-color: transparent;
  color: inherit;
}

address {
  font-style: normal;
}

html,
body {
  height: 100%;
}

body {
  color: var(--black-color);
  display: flex;
  flex-direction: column;
  font-family: var(--font-family, sans-serif);
  font-size: 18px;
  line-height: 1.5;
}

.lock {
  overflow: hidden;
  touch-action: none;
}

.page {
  scroll-behavior: smooth;
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
}

.main {
  flex-grow: 1;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.container {
  max-width: var(--container-width);
  padding: 0 var(--container-offset);
  margin: 0 auto;
}
.container--narrow {
  max-width: var(--container-width-narrow);
}

.logo__img {
  object-fit: contain;
}

.sr-only {
  border: 0;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  padding: 0;
  position: absolute;
  height: 1px;
  margin: -1px;
  overflow: hidden;
  white-space: nowrap;
  width: 1px;
}

.visually-hidden {
  clip: rect(0 0 0 0);
  overflow: hidden;
  position: absolute;
  height: 1px;
  width: 1px;
}

.burger {
  display: none;
  position: relative;
  width: 44px;
  height: 44px;
}
.burger__line, .burger:before, .burger:after {
  content: "";
  background-color: var(--main-color-dark);
  position: absolute;
  width: 100%;
  height: 2px;
  width: 30px;
  left: 7px;
  transition: transform 0.4s;
}
.burger:before {
  top: 12px;
}
.burger:after {
  bottom: 10px;
}
.burger--active::before {
  transform: rotate(45deg);
  top: 21px;
}
.burger--active::after {
  transform: rotate(-45deg);
  bottom: 21px;
}
.burger--active .burger__line {
  transform: scale(0);
}

.search-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  outline: transparent;
}
.search-btn:focus svg {
  fill: var(--red-hover);
}
.search-btn:hover svg {
  fill: var(--red-hover);
}
.search-btn svg {
  width: 24px;
  height: 24px;
  fill: var(--accent-color);
  transition: fill 0.3s ease 0s;
}

.btn {
  padding: 17px 60px;
  border: 1px solid transparent;
  border-radius: 50px;
}
.btn--transparent {
  padding: 13px 40px;
  border-color: var(--accent-color);
  transition: color 0.3s ease 0s, background-color 0.3s ease 0s, box-shadow 0.3s ease 0s;
  outline: transparent;
}
.btn--transparent:focus {
  color: var(--white-color);
  background-color: var(--red-hover);
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.25);
}
.btn--transparent:hover {
  color: var(--white-color);
  background-color: var(--accent-color);
}

.logo {
  outline: transparent;
  transition: all 0.3s ease 0s;
  border-radius: 50%;
}
.logo:focus {
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.25);
}
.logo:hover {
  opacity: 0.8;
}

.social-list {
  display: flex;
}
.social-list__item:not(:last-child) {
  margin-right: 25px;
}
.social-list__link {
  display: flex;
  justify-content: center;
  align-items: center;
  outline: transparent;
}
.social-list__link:focus svg {
  fill: var(--pink);
}
.social-list__link:hover svg {
  fill: var(--dark-grey-color);
}
.social-list__link svg {
  transition: fill 0.3s ease 0s;
  width: 24px;
  height: 24px;
  fill: var(--white-color);
}

.switch-color {
  display: flex;
  justify-content: center;
  align-items: center;
}
.switch-color:focus svg {
  fill: var(--pink);
}
.switch-color:hover svg {
  fill: var(--dark-grey-color);
}
.switch-color svg {
  width: 24px;
  height: 24px;
  fill: var(--white-color);
  transition: fill 0.3s ease 0s;
}

.menu {
  display: flex;
}
.menu__item:not(:last-child) {
  margin-right: 50px;
}
.menu__link {
  transition: color 0.3s ease 0s;
  outline: transparent;
}
.menu__link:focus {
  color: var(--red-hover);
}
.menu__link:hover {
  color: var(--accent-color);
}

.header__top {
  font-family: "OpenSans";
  font-weight: 400;
  background-color: var(--accent-color);
  padding: 12px 0;
}
.header__top-wrapper {
  color: var(--white-color);
  display: flex;
  font-size: 16px;
  justify-content: space-between;
  align-items: center;
}
.header__adress {
  margin-right: 20px;
}
.header__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__switch-color {
  margin-right: 55px;
}
.header__link {
  display: block;
  margin-right: 55px;
  transition: color 0.3s ease 0s;
  outline: transparent;
}
.header__link:focus {
  color: var(--pink);
}
.header__link:hover {
  color: var(--dark-grey-color);
}
.header__bottom {
  background-color: var(--pink-light);
  font-family: "OpenSans";
  font-weight: 400;
  padding: 5px 0;
}
.header__bottom-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__box {
  display: flex;
}
.header__search {
  margin-right: 40px;
}
.footer {
  background-color: var(--accent-color);
  color: var(--white-color);
  font-size: 16px;
  line-height: 1.2;
  padding: 40px 0;
}
.footer__wrapper {
  display: flex;
  justify-content: space-between;
}
.footer__item:nth-child(1) {
  max-width: 245px;
  width: 100%;
}
.footer__item:nth-child(2) {
  max-width: 225px;
  width: 100%;
}
.footer__item:nth-child(3) {
  max-width: 285px;
  width: 100%;
}
.footer__item:nth-child(4) {
  max-width: 225px;
  width: 100%;
}
.footer__item:nth-child(2), .footer__item:nth-child(3), .footer__item:nth-child(4) {
  margin-top: 10px;
}
@supports (margin-right: clamp( 3.125rem , 1.7551369863rem  +  6.8493150685vw , 9.375rem )) {
  .footer__item:not(:last-child) {
    margin-right: clamp( 3.125rem , 1.7551369863rem  +  6.8493150685vw , 9.375rem );
  }
}
@supports not (margin-right: clamp( 3.125rem , 1.7551369863rem  +  6.8493150685vw , 9.375rem )) {
  .footer__item:not(:last-child) {
    margin-right: calc(
			3.125rem + 6.25 * (100vw - 20rem) / 91.25
		);
  }
}
.footer__logo {
  display: block;
  margin: 0 0 25px -13px;
}
.footer__title {
  font-size: 18px;
  margin-bottom: 25px;
}
.footer__nav {
  display: flex;
}
@supports (margin-right: clamp( 1.25rem , 0.5650684932rem  +  3.4246575342vw , 4.375rem )) {
  .footer__menu:not(:last-child) {
    margin-right: clamp( 1.25rem , 0.5650684932rem  +  3.4246575342vw , 4.375rem );
  }
}
@supports not (margin-right: clamp( 1.25rem , 0.5650684932rem  +  3.4246575342vw , 4.375rem )) {
  .footer__menu:not(:last-child) {
    margin-right: calc(
			1.25rem + 3.125 * (100vw - 20rem) / 91.25
		);
  }
}
.footer__menu-item:not(:last-child) {
  margin-bottom: 15px;
}
.footer__list-item:not(:last-child) {
  margin-bottom: 15px;
}
.footer__social-list {
  margin-bottom: 40px;
}
.footer__switch-color svg {
  margin-right: 10px;
}

.copy {
  display: block;
  margin-bottom: 15px;
}

.politic,
.footer__menu-link,
.footer__link,
.footer__switch-color {
  outline: transparent;
  transition: color 0.3s ease 0s;
}
.politic:focus,
.footer__menu-link:focus,
.footer__link:focus,
.footer__switch-color:focus {
  color: var(--pink);
}